.contact-us form {
    margin: 17px 0 0 50px;
}

.contact-us p {
    margin-left: 50px;
    margin-right: 50px;
}

.contact-us form input, .contact-us form textarea {
    width: 80%;
    padding: 8px 15px;
    background-color: #142C3F;
    border: none;
    outline: none;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    font-family: 'PT Sans', Helvetica, Arial, sans-serif;
    font-size: 16px;
    color: #aaa;
    margin-bottom: 8px;
    box-shadow: inset 0 2px 1px rgba(12, 12, 12, 0.40);
    transition: background-color linear .2s,box-shadow linear .2s;
}

.contact-us form input:focus, .contact-us form textarea:focus {

    box-shadow: inset 0 2px 1px rgba(0,0,0,0.66);
    background-color: #265173;
}

.contact-us form button {
    margin: 2px 0 0 0px;
    padding: 7px 15px;
    background: #3ec3d5;
    border: 0;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    font-family: 'PT Sans', Helvetica, Arial, sans-serif;
    font-size: 16px;
    color: #fff;
    transition: all .2s;
    -o-transition: all .2s;
    -moz-transition: all .2s;
    -webkit-transition: all .2s;
    -ms-transition: all .2s;
}

.contact-us form button:hover {
    background: #222;
    color: #aaa;
}

.contact-us form input:-moz-placeholder { color: #aaa; }
.contact-us form input:-ms-input-placeholder { color: #aaa; }
.contact-us form input::-webkit-input-placeholder { color: #aaa; }

.social-links {
    margin: 40px 50px 0 50px;
    padding-top: 40px;
    border-top: 1px dotted #4d4d4d;
    text-align: center;
}

.copyright {
    margin-top: 20px;
    text-align: center;
}




.social-links a {
    display: inline-block;
    position: relative;
    /*float: left;*/
    width: 50px;
    height: 50px;
    margin: 5px;
}
.social-links a article {
    cursor: pointer;
    display: block;
    border-radius: 50%;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #788D94;
    box-shadow: 0 0 6px -2px #000, 0 0 45px -20px #000;
    color: #24323c;
    line-height: 90px;
    transform: perspective(300px) rotateY(0deg);
    transition: transform 0.4s linear, background-color 0s linear 0.2s, color 0s linear 0.2s;
}
.social-links a:hover article {
    transform: perspective(300px) rotateY(180deg);
    color: #eee;
    transition: transform 0.2s linear, background-color 0s linear 0.1s, color 0s linear 0.1s;
}
.social-links a:nth-child(1):hover article {
    background-color: #3B5998;
}
.social-links a:nth-child(2):hover article {
    background-color: #00ACED;
}
.social-links a:nth-child(3):hover article {
    background-color: #2C4762;
}
.social-links a:nth-child(4):hover article {
    background-color: #CB2027;
}
.social-links a:nth-child(5):hover article {
    background-color: #EA4C89;
}
.social-links a:nth-child(6):hover article {
    background-color: #4183C4;
}
.social-links a:nth-child(7):hover article {
    background-color: #3399CC;
}
.social-links a:nth-child(8):hover article {
    background-color: #b7110a;
}
.social-links a:nth-child(9):hover article {
    background-color: #445566;
}
.social-links a:nth-child(10):hover article {
    background-color: #3399CC;
}
.social-links a:nth-child(11):hover article {
    background-color: #F26300;
}
.social-links a:nth-child(12):hover article {
    background-color: #3F729B;
}

.social-links a article span {
    line-height: 50px;
    font-size: 33px;
    transition: transform 0s linear 0.2s, text-shadow 0s linear 0.2s;
    display: block;
    -webkit-font-smoothing: subpixel-antialiased;
    font-smoothing: antialiased;
}
.social-links a:hover span {
    transition: transform 0s linear 0.1s, text-shadow 0s linear 0.1s;
    transform: scale(-1, 1);
    text-shadow: 0 0 50px rgba(255, 255, 255, 0.5);
}
#back-to-top{
    position: fixed;
    border-radius: 50%;
    right: 15px;
    bottom: 15px;
    width: 45px;
    height: 45px;
    line-height: 0px;
    color: #fff;
    background: rgba(76, 76, 76, 0.40);
    display: none;
    box-shadow: none !important;
    z-index: 99;
    padding: 10px;
    padding-top: 12px;
    font-size: 18px;
}
#back-to-top:hover{
    background: rgba(76, 76, 76, 0.86);
    color: #3ec3d5;
}
::selection{
    background: #3ec3d5;
}

